export default {
  props: {
    user: {
      type: Object,
      required: true
    }
  },
  methods: {
    download: function download() {
      this.$router.push({
        path: "/download"
      });
    },
    gotoUser: function gotoUser(id) {
      this.$router.push({
        path: "/user/playlist",
        query: {
          uid: id
        }
      });
    }
  }
};